import { deCentralization,lazyComponent} from '@/utils/util'
import crmRouter from '../crm/index.js'

// 
const routers = deCentralization(require.context('./', true, /^\.\/(?!\/)[\s\S]+\/router\.js$/),false)

routers.push({
  path: '/app',
  redirect: '/home'
})

export default [{
  path: '/app',
  name: 'layout',
  component: r => require(['@/common/layout/'],r),
  meta: { auth: true },
  children: [...routers, ...crmRouter]
}]


