export default [{
  meta: {
    name: '404'
  },
  path: '/404',
  name: '404',
  component: ()=>import('@/common/page404.vue')
}]

export const notFound = path => {
  return [{
    path: path,
    component: ()=>import('@/common/page404.vue')
  }]
}


