import {lazyComponent} from '@/utils/util'
// query.id

export default[
    {
        path: '/knowledgeList',
        name: 'knowledgeList',
        component: lazyComponent('knowledge/list',true),
        meta: {
            title: '',
            name:''
        }
    },
    {
        path: '/knowledgeNew',
        name: 'knowledgeNew',
        component: lazyComponent('knowledge/new',true),
        meta: {
            title: '',
            name:''
        }
    },
    {
        path: '/knowledgeEdit',
        name: 'knowledgeEdit',
        component: lazyComponent('knowledge/new',true),
        meta: {
            title: '',
            name:''
        }
    },
    {
        path: '/knowledgeDetail/:knowledgeId',
        name: 'knowledgeDetail',
        component: lazyComponent('knowledge/detail',true),
        meta: {
            title: '',
            name:''
        }
    },
]


