import axios from '@/utils/fetch'
const MODULE = 'project'
/**
 * 
 * @export
 * @param {any} params 
 * @returns 
 */
export function getProjectList(params) {
    return axios({
        url: `${MODULE}/paging`,
        params,
    })
}
/**
 * 
 * @export
 * @param {any} params 
 * @returns 
 * @method {GET}
 */
export function getProjectByName(params) {
    return axios({
        url: `${MODULE}/search`,
        params,
    })
}
/**
 * Id
 * @export
 * @param {any} params 
 * @returns 
 * @method {DELETE}
 */
export function deleteProjectById(id) {
    return axios({
        url: `${modelUrl}/${id}`,
        method:'DELETE'
    })
}
/**
 * Id
 * @export
 * @param {any} params 
 * @returns 
 * @method {GET}
 */
export function getProjectDetail(id) {
    return axios({
        url: `${MODULE}/detail/${id}`,
    })
}
/**
 * 
 * @export
 * @param {any} params 
 * @returns 
 * @method {POST}
 */
export function addProject(data) {
    return axios({
        url: `${MODULE}`,
        data: data,
        method:'POST'
    })
}
/**
 * 
 * @export
 * @param {any} params 
 * @returns 
 * @method {POST}
 */
export function updateProjetMan(params) {
    return axios({
        url: `${MODULE}/${id}/director`,
        data: params,
        method:'PUT'
    })
}



