import Vue from 'vue'
import Vuex from 'vuex'
import state from './state'
import mutations from './mutations'
import actions from './actions'
import getters from './getters'

import department from '../views/organization/department/store'
import employees from '../views/organization/employees/store'

Vue.use(Vuex)
const store = new Vuex.Store({
  state,
  mutations,
  actions,
  getters,
		modules: {
    department,
    employees,
  }
})
export default store


