import axios from '@/utils/fetch'
const modelUrl = 'plan'
/**
 * 
 * @export
 * @param {any} {,,,,} 
 * @returns 
 */
export function getPlanList ({plan_type,start_time,end_time,page_num,page_size}) {
    return axios({
        url: `${modelUrl}/paging`,
        params: {plan_type,start_time,end_time,page_num,page_size}
    });
}
/**
 * 
 * @export 
 * @param {any} id Id
 * @param {any} {,} 
 * @returns 
 */
export function putPlanSummary (id,{plan_summary,attaches}) {
    return axios({
        url: `${modelUrl}/${id}/summary`,
        data: {plan_summary,attaches},
        method:'PUT'
    });
}
/**
 * 
 * @export
 * @param {any} {,,,,,,} 
 * name	String		
 * path	String	URL	
 * size Integer 	Byte
 * @returns 
 */
export function postNewPlan ({plan_type,start_time,end_time,plan_state,plan_title,plan_content,attaches}) {
    return axios({
        url: `${modelUrl}`,
        data: {plan_type,start_time,end_time,plan_state,plan_title,plan_content,attaches},
        method:'POST'
    });
}
/**
 * 
 * @export
 * @param {any} {plan_type,start_time,end_time,plan_state,plan_title,plan_content,attaches} 
 * @returns 
 */
export function postEditPlan ({plan_type,start_time,end_time,plan_state,plan_title,plan_content,attaches,id}) {
    return axios({
        url: `${modelUrl}`,
        data: {plan_type,start_time,end_time,plan_state,plan_title,plan_content,attaches,id},
        method:'PUT'
    });
}
/**
 * Id
 * @export
 * @param {any} id 
 * @returns 
 */
export function delPlan(id){
    return axios({
        url:`${modelUrl}/${id}`,
        method:'DELETE'
    })
}
/**
 * Id
 * @export
 * @param {any} id Id	
 * @returns 
 */
export function getPlanDetail(id){
    return axios({
        url:`${modelUrl}/detail/${id}`
    })
}



