import axios from '@/utils/fetch'
const PROVINCE = 'area/province'
const CITY = 'area/city'

// 
let provinceList = []
let cityCache = {}
/**
 * 
 */
export function getProvinceList() {
    if (provinceList && provinceList.length) {
        return Promise.resolve(provinceList)
    }
    return axios({
        url: PROVINCE
    }).then(data => {
        provinceList = data
        return provinceList
    })
}
/**
 * 
 */
export function getCityList(params) {
    let _code = params.org_code
    if (cityCache[_code]) {
        return Promise.resolve(cityCache[_code])
    }
    return axios({
        url: CITY,
        params
    }).then(data => {
        cityCache[_code] = data
        return cityCache[_code]
    })
}




