import * as type from './mutationTypes'
import {Message} from 'element-ui'

import * as api from '@/api/organization'

const showErr = (text) => {
  Message.error({
    message: text || 'err',
    showClose: true,
    duration: 4000
  })
}
const showSuccess = (text) => {
  Message.success({
    message: text || 'success',
    showClose: true,
    duration: 4000
  })
}

const actions = {
  async getEmployeesList({commit}, params) {
    try {
      const result = await api.getEmployeesList(params)
      commit(type.UPDATE_EMPLOYEES_LIST, result)
    } catch (err) {
      showErr(err)
    }
  },
  async postEmployees({commit}, params) {
    await api.postEmployees(params)
    showSuccess()
  },
  async getEmployeesInfo({commit}, params) {
    return await api.getEmployeesInfo(params)
  },
  async changeEmployeesStatus({commit}, params) {
    try {
      await api.changeEmployeesStatus(params)
      showSuccess()
    } catch (err) {
      showErr(err)
    }
  },
  async closeEmployees({commit}, params) {
    try {
      await api.closeEmployees(params)
      showSuccess()
    } catch (err) {
      showErr(err)
    }
  },
  async resetEmployeesPwd({commit}, params) {
    try {
      await api.resetEmployeesPwd(params)
      showSuccess()
    } catch (err) {
      showErr(err)
    }
  },
  async putEmployees({commit}, params) {
    await api.putEmployees(params)
    showSuccess()
  }
}

const mutations = {
  [type.UPDATE_EMPLOYEES_LIST](state, data) {
    state.employeesList = data
  },
}

const state = {
  employeesList: {}
}

const getters = {
  employeesList: (state) => {
    return state.employeesList
  }
}


export default {
  state,
  getters,
  mutations,
  actions
}


