import axios from '@/utils/fetch.js'

const KNOWLEDGE_PAGING = 'knowledge/paging'
const KNOWLEDGE = 'knowledge'
const KNOWLEDGE_TAG_SEARCH = 'knowledge_tag/search'

export function getKnowledgeList(params) {
    return axios({
        url: KNOWLEDGE_PAGING,
        params: params,

    })
}
export function editKnowledgeTagName(id,data) {
    return axios({
        url: `knowledge_tag/${id}/tag_name`,
        data: data,
        methods: 'POST'
    })
}
export function addKnowledge(data) {
    return axios({
        url: KNOWLEDGE,
        data: data,
        methods: 'POST'
    })
}
export function getKnowledgeByTag(params) {
    return axios({
        url: KNOWLEDGE_TAG_SEARCH,
        params: params,
    })
}
export function deleteKnowledgeById(id,data) {
    return axios({
        url: `knowledge_tag/${id}`,
        data: data,
        methods: 'DELETE'
    })
}
export function getKnowledgeDetailById(id) {
    return axios({
        url: `knowledge/detail/${id}`,
    })
}


