import axios from '@/utils/fetch.js'

const SERVICE_PAGING = 'service/paging'
const SERVICE = 'service'

export function getServiceList(params) {
    return axios({
        url: SERVICE_PAGING,
        params: params,
    })
}
export function addService(data) {
    return axios({
        url: SERVICE,
        data: data,
        method: 'POST'
    })
}
export function editService(data) {
    return axios({
        url: SERVICE,
        data: data,
        method: 'PUT'
    })
}

export function deleteService(id) {
    return axios({
        url: 'service/' + id,
        method: 'DELETE'
    })
}
