import axios from '@/utils/fetch'
const modelUrl = 'visit_record'
/**
 * 
 * 
 * @export
 * @param {any} {,,,,} 
 * @returns 
 */
export function getVisitRecord({customer_name,project_name,visit_purpose,page_num,page_size}){
    return axios({
        url:`${modelUrl}/paging`,
        params:{customer_name,project_name,visit_purpose,page_num,page_size}
    })
}
/**
 * 
 * @export
 * @param {any} params 
 * @returns 
 */
export function postNewVisit(params){
    return axios({
        url:`${modelUrl}`,
        data:params,
        method:'POST'
    })
}
/**
 * Id
 * @export
 * @param {any} id 
 * @returns 
 */
export function getVisitDetail(id){
    return axios({
        url:`${modelUrl}/detail/${id}`
    })
}
/**
 * 
 * 
 * @export
 * @param {any} params 
 * @returns 
 */
export function putEditVisit({id,visit_purpose, visit_time, linkman_name, linkman_contact, linkman_post, visit_result, visit_promote, support_typ}){
 return axios({
        url:`${modelUrl}`,
        data:{id,visit_purpose, visit_time, linkman_name, linkman_contact, linkman_post, visit_result, visit_promote, support_typ,},
        method:'PUT'
    })
}


