import axios from 'axios'
import config from '@/config'
import {removeEmpty} from '@/utils/util'
import {Message, MessageBox} from 'element-ui'

let _env = process.env.NODE_ENV
let _envConfig = config
// 
axios.defaults.baseURL = _envConfig.host;
axios.defaults.timeout = 10000;
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';
// axios.defaults.transformRequest = [function (data) {

// }]
axios.interceptors.request.use(function (config) {
  if (config.params && Object.keys(config.params).length) {
    config.params = removeEmpty(config.params)
  }
  if (config.data && Object.keys(config.data).length) {
    config.data = removeEmpty(config.data)
  }
  config.headers['mac_key'] = `test_key_123456`
  return config;
}, function (error) {
  return Promise.reject(error);
});
axios.interceptors.response.use(function (response) {
  if (response.config.method != 'get') {
    Message.success({
      message: '',
      showClose: true,
      duration: 4000
    })
  }
  return response;
}, function (error) {
  if (error.response.data) {
    return Promise.reject(error.response.data['err_msg'] || error.response.data['err_code']);
  }
  return Promise.reject(error);
});
export default (options) => {
  return new Promise((resolve, reject) => {
    // options.url = 'prm_business/v1.0/' + options.url
    let _user = {} // :todo
    let _params = {}
    let _conf = null
    if (!options) return Message.error('')

    // method
    if (!options.method || options.method == 'get') {
      if (options.params) {
        options.params = Object.assign(_params, options.params)
      } else {
        options.params = {..._params}
      }
    } else if (options.method == 'post' || options.method == 'put' || options.method == 'patch') {
      if (options.data) {
        options.data = Object.assign(_params, options.data)
      } else {
        options.data = {..._params}
      }
    }

    _conf = Object.freeze(options)

    axios(_conf)
      .then(rsp => {
        resolve(rsp.data)
      })
      .catch((error) => {
        // MessageBox({
        //   title: '',
        //   message: error.response.data.err_msg,
        //   duration: 1000,
        //   showClose: true
        // })
        reject(error)
      })
  })
}


