import Vue from 'vue'
// import Moment from 'moment'
import {fmt} from '@/utils/util'

// 
Vue.filter('dateFormat', function (input, type) {
	return fmt.date(input,type)
});

// 
Vue.filter('supportType', function (input) {
	let text
	switch (input) {
		case 1:
			text = ''
			break
		case 2:
			text = ''
			break
		case 3:
			text = ''
			break
		default:
			text = ''
	}
	return text
});
Vue.filter('planType', function (input) {
	let text
	switch (input) {
		case 1:
			text = ''
			break
		case 2:
			text = ''
			break
		case 3:
			text = ''
			break
		default:
			text = ''
	}
	return text
});
Vue.filter('planState', function (input) {
	let text
	switch (input) {
		case 1:
			text = ''
			break
		case 2:
			text = ''
			break
		case 3:
			text = ''
			break
		default:
			text = ''
	}
	return text
});
Vue.filter('customerLevel', function (input) {
	let text
	switch (input) {
		case 1:
			text = 'VIP'
			break
		case 2:
			text = ''
			break
		case 2:
			text = ''
			break
		default:
			text = ''
	}
	return text
});



