import http from '@/utils/fetch'

export const getTree = () => {
  return http({
    url: 'eam/v1.0/department_trees'
  })
}

export const getDepartmentInfo = (id) => {
  return http({
    url: `eam/v1.0/departments/${id}`
  })
}

export const postNewDepartment = (data) => {
  return http({
    url: `eam/v1.0/departments`,
    method: 'POST',
    data
  })
}

export const editDepartment = (data) => {
  return http({
    url: `eam/v1.0/departments`,
    method: 'PUT',
    data
  })
}

export const deleteDepartment = (id) => {
  return http({
    url: `eam/v1.0/departments/${id}`,
    method: 'DELETE'
  })
}

export const getEmployeesList = (params) => {
  if (params.id) {
    console.log('++++')
    const id = params.id
    const paramsFinal = paramsHandler(params, 'id')
    console.log(paramsFinal)
    return http({
      url: `eam/v1.0/department/${id}/staffs${paramsFinal}`
    })
  }
}

export const getEmployeesInfo = (id) => {
  if (id) {
    return http({
      url: `eam/v1.0/staffs/${id}`
    })
  }
}

export const postEmployees = (data) => {
  return http({
    url: `eam/v1.0/staffs`,
    method: 'POST',
    data
  })
}

export const putEmployees = (data) => {
  return http({
    url: `eam/v1.0/staffs`,
    method: 'PUT',
    data
  })
}

export const changeEmployeesStatus = (params) => {
  return http({
    url: `eam/v1.0/staffs/${params.id}/${params.status}`,
    method: 'PUT'
  })
}

export const closeEmployees = (data) => {
  return http({
    url: `eam/v1.0/staffs/batch_close`,
    method: 'DELETE',
    data
  })
}

export const resetEmployeesPwd = (data) => {
  return http({
    url: `eam/v1.0/staffs/batch_reset`,
    method: 'PUT',
    data
  })
}

export const getStaffTree = () => {
  return http({
    url: `eam/v1.0/common/enterprise_staff_trees`,
  })
}

function paramsHandler(params, arr) {
  if (arr) {
    if (typeof arr === 'string') {
      delete params[arr]
    } else if(arr instanceof Array) {
      arr.map((key) => {
        delete params[key]
      })
    }
  }
  const paramsArr = []
  Object.keys(params).forEach((key) => {
    if (params[key] || params[key] === 0) {
      paramsArr.push(`${key}=${params[key]}`)
    }
  })
  return '?' + paramsArr.join('&')
}


