// css
import 'element-ui/lib/theme-chalk/index.css'
import '@/scss/style.scss';
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'

// base The Vue build version to load with the `import` command (runtime-only or
// standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import ElementUI from 'element-ui'
import VueQuillEditor from 'vue-quill-editor'
// import { mapMutations } from 'vuex' import { setUserInfo } from
// '@/store/mutation-types.js'
import store from '@/store'
import router from '@/router'
import '@/filter'
import Api from '@/api'
import App from '@/common/App'
import {browserCheck, browserCheckNotice,removeEmpty} from '@/utils/util'
// vue-echarts git https://github.com/Justineo/vue-echarts
import ECharts from 'vue-echarts/components/ECharts.vue'
import 'echarts/lib/chart/bar'
import 'echarts/lib/chart/pie'
import 'echarts/lib/chart/line'
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/legend'
import 'echarts/lib/component/title'
import VueAMap from 'vue-amap';
import VueAsset from 'asset-web-support'

// 
if (!browserCheck()) {
    browserCheckNotice()
}

Vue.use(ElementUI)
Vue.use(VueAMap);
Vue.use(VueAsset, {
    components: {
        editor: {},
        upload: {
            imageUpload: {
                action: 'http://baidu.com' // action
            }
        }
    },
    plugins: {
        // axios  false
        axios:false
        // axios: {
        //     transformResponse: {
        //         hump: false
        //     },
        //     interceptor: {
        //         // errorHandle: {
        //         //     // 
        //         //     errorHandle: error => {
                        
        //         //     }
        //         // }
        //         request:(config)=>{
        //             console.log(config)
        //             // config.params= removeEmpty(config.params)
        //         }
        //     }
        // }
    }
})
Vue.use(VueQuillEditor)
Vue.component('chart', ECharts)
Vue.prototype.$api = Api
// 50M10

/* eslint-disable no-new */
const appVue = new Vue({
    el: '#app',
    store,
    router,
    template: '<App></App>',
    components: {
        App
    },
    methods: {
        // ...mapMutations({ setUserInfo: setUserInfo }),
    },
    created() {},
    mounted() {}
})

export default appVue;


