import * as type from './mutationTypes'
import {Message} from 'element-ui'

import * as api from '@/api/organization'

const showErr = (text) => {
  Message.error({
    message: text,
    showClose: true,
    duration: 4000
  })
}
const showSuccess = (text) => {
  Message.success({
    message: text,
    showClose: true,
    duration: 4000
  })
}

const actions = {
  async getDepartmentTree({commit}) {
    try {
      const result = await api.getTree()
      commit(type.UPDATE_TREE, result)
    } catch (err) {
      showErr(err)
    }
  },
  async deleteDepartment({commit}, id) {
    try {
      await api.deleteDepartment(id)
      showSuccess('success')
    } catch (err) {
      showErr(err)
    }
  },
  async getDepartment({commit}, id) {
    try {
      const result = await api.getDepartmentInfo(id)
      commit(type.UPDATE_DEPARTMENT_INFO, result)
    } catch (err) {
      showErr(err)
    }
  },
  async postDepartment({commit}, data) {
    try {
      await api.postNewDepartment(data)
      showSuccess('success')
    } catch (err) {
      showErr(err)
    }
  },
  async editDepartment({commit}, data) {
    try {
      await api.editDepartment(data)
      showSuccess('success')
    } catch (err) {
      showErr(err)
    }
  },
  async getStaffTree({commit}) {
    try {
      const result = await api.getStaffTree()
      commit(type.UPDATE_STAFF_TREE, result)
      showSuccess('success')
    } catch (err) {
      showErr(err)
    }
  }
}

const mutations = {
  [type.UPDATE_TREE](state, data) {
    state.tree = data
  },
  [type.UPDATE_DEPARTMENT_INFO](state, data) {
    state.departmentInfo = data
  },
  [type.UPDATE_STAFF_TREE](state, data) {
    state.staffTree = data
  },
}

const state = {
  tree: [],
  departmentInfo: {},
  staffTree: []
}

const getters = {
  departmentTree: (state) => {
    return state.tree
  },
  departmentInfo: (state) => {
    return state.departmentInfo
  },
  staffTree: (state) => {
    return state.staffTree
  }
}


export default {
  state,
  getters,
  mutations,
  actions
}


