import config from '@/config'
import {flatten, cloneDeep} from 'lodash'


export const showConfirm = function(_this, msg, callback) {
  _this.$confirm(msg, '', {
    confirmButtonText: '',
    cancelButtonText: '',
    type: 'warning'
  }).then(() => {
    if (callback) {
      Promise.resolve(callback.call(_this)).then(() => {
        _this.$message({
          type: 'success',
          message: '!'
        });
      })
    }
  }).catch((err) => {
    _this.$message({
      type: 'info',
      message: `,${err}`
    });
  });
}
export const utilCookie = {
  set: function (name, value, days) {
    var d = new Date;
    d.setTime(d.getTime() + 24 * 60 * 60 * 1000 * days);
    window.document.cookie = name + "=" + value + ";path=/;expires=" + d.toGMTString();
  },
  get: function (name) {
    var v = window.document.cookie.match('(^|;) ?' + name + '=([^;]*)(;|$)');
    return v ? v[2] : null;
  },
  delete: function (name) {
    this.set(name, '', -1);
  }
};

// 
export const array2tree = (array, pflag = 'pid', sflag = 'id', root = 0) => {
  function cascader(pid) {
    const target = []
    array.forEach(a => {
      const _clone = Object.assign({}, a)
      if (_clone[pflag] == pid) {
        target.push(_clone)
        const _child = cascader(_clone[sflag])
        if (_child.length)
          _clone.children = _child
      }
    });
    return target
  }

  return cascader(root)
};

export const getParentsIds = (list, leafId, key = 'id', f_key = 'fid') => {
  const ids = [], maps = {}
  list.forEach(n => {
    maps[n[key]] = n
  })
  while (maps[leafId]) {
    ids.push(leafId.toString())
    leafId = maps[leafId][f_key]
  }
  return ids
}
export const getArrayIds = (list, key) => {
  let arry = [];
  list.forEach(n => {
    arry.push(n[key])
  })
  return arry.join(",")
}

export const copySameNamePropety = (to, from) => {
  for (let key in to) {
    to[key] = from[key]
  }
  return to;
}

export const getCurDate = () => {
  var date = new Date();
  var seperator1 = "-";
  var seperator2 = ":";
  var month = date.getMonth() + 1;
  var strDate = date.getDate();
  if (month >= 1 && month <= 9) {
    month = "0" + month;
  }
  if (strDate >= 0 && strDate <= 9) {
    strDate = "0" + strDate;
  }
  var hours = date.getHours() >= 10 ? date.getHours() : "0" + date.getHours();
  var minutes = date.getMinutes() >= 10 ? date.getMinutes() : "0" + date.getMinutes();
  var seconds = date.getSeconds() >= 10 ? date.getSeconds() : "0" + date.getSeconds();
  var currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate
    + " " + hours + seperator2 + minutes
    + seperator2 + seconds;
  return currentdate;
}

export const getDateFormat = (e) => {
  let date = e;
  var seperator1 = "-";
  var seperator2 = ":";
  var month = date.getMonth() + 1;
  var strDate = date.getDate();
  if (month >= 1 && month <= 9) {
    month = "0" + month;
  }
  if (strDate >= 0 && strDate <= 9) {
    strDate = "0" + strDate;
  }
  var hours = date.getHours() >= 10 ? date.getHours() : "0" + date.getHours();
  var minutes = date.getMinutes() >= 10 ? date.getMinutes() : "0" + date.getMinutes();
  var seconds = date.getSeconds() >= 10 ? date.getSeconds() : "0" + date.getSeconds();
  var currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate
    + " " + hours + seperator2 + minutes
    + seperator2 + seconds;
  return currentdate;
}

export const print = (obj) => {
  var newWindow = window.open("", "_blank");
  newWindow.document.write(window.document.head.innerHTML)
  newWindow.document.write(obj.innerHTML)
  //
  newWindow.document.close();
  //
  setTimeout(function () {
    newWindow.print();
    newWindow.close();
  }, 500)
}

// webpack require.context  

// export  default
export const webpackRequire2array = (r, exinclude) => {
  let contents = []
  const paths = r.keys().filter((p) => {
    return exinclude.indexOf(p) == -1
  });
  for (let p of paths) {
    let fn = r(p).default ? r(p).default : r(p)
    if (fn instanceof Array) {
      contents = [...contents, ...fn]
    } else if (fn instanceof Object) {
      contents.push(fn)
    }
  }
  return contents
}
// webpack require.context  
export const webpackRequire2obj = (r, exinclude) => {
  let contents = {}
  const paths = r.keys().filter((p, i) => {
    return exinclude.indexOf(p) == -1
  });
  for (let p of paths) {
    let fn = r(p).default ? r(p).default : r(p)
    let k = p.match(/(\w+-?\w+).js$/)[1]
    contents[k] = fn
  }
  return contents
}

