import {notFound} from '@/router/error/router'

export const deCentralization = (files, defaultPath = '*') => {
  const routes = flatten((r => {
    const paths = r.keys().filter(p => {
      return p.split('/').length === 3
    })
    return paths.map(k => {
      return r(k).default
    })
  })(files))
  if (defaultPath) {
    routes.push(...notFound(defaultPath))
  }
  return routes
}

