export const subStringLimit = (str, num) => {
  if (str.length <= num)
    return str

  return str.substring(0, num) + '...'
}
export const fmt = {
  /**
   * 
   * @param {any} date
   * @param {string} [format='yyyy-MM-dd hh:mm:ss']
   * @returns
   */
  date(date, format = 'yyyy-MM-dd hh:mm:ss') {
    if (!date) {
      return date
    } else if (/^\d+$/.test(+date)) {
      // javatimestamp
      return fmt.dateFormat(new Date(+date * 1000), format)
    } else if (typeof date === 'object' && date.time) {
      // javaobject
      return fmt.dateFormat(new Date(date.time), format)
    } else if ((/date/i).test(date)) {
      // .nettimestamp
      return fmt.dateFormat(new Date(date.match(/\d+/)[0]), format)
    } else if (date && typeof date === 'string' && /\.\d$/.test(date)) {
      // mysql
      return fmt.dateFormat(new Date(date.replace(/\.\d$/, '').replace(/-/g, '/')), format)
    }
    return date
  },
  /**
   * 
   * @param {any} date
   * @param {any} format
   * @returns
   */
  dateFormat(date, format) {
    let o = {
      'M+': date.getMonth() + 1, // month
      'd+': date.getDate(), // day
      'h+': date.getHours(), // hour
      'm+': date.getMinutes(), // minute
      's+': date.getSeconds(), // second
      'q+': Math.floor((date.getMonth() + 3) / 3), // quarter
      'S': date.getMilliseconds() // millisecond
    }
    if (/(y+)/.test(format)) {
      format = format.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
    }
    for (let k in o) {
      if (new RegExp('(' + k + ')').test(format)) {
        format = format.replace(RegExp.$1, RegExp.$1.length === 1 ?
          o[k] :
          ('00' + o[k]).substr(('' + o[k]).length))
      }
    }
    return format
  },
  getTime(string) {
    let date = new Date(string)
    let floorDate = Math.floor(date.getTime() / 1000)
    return floorDate
  }
}
export const eachOwn = (obj, fn) => {
  for (let key in obj) {
    if (obj.hasOwnProperty(key)) {
      fn(obj[key], key, obj)
    }
  }
}
export const removeEmpty = (data) => {
  eachOwn(data, (value, key, data) => {
    if (value === undefined || value === null || (typeof value === 'string' && value.trim() === '')) {
      delete data[key]
    }
  })
  return data
}

export const transformCamel = function (data) {
  if (typeof data === 'object') {
    if (data instanceof Array) {
      let arr = []
      data.map(d => {
        arr.push(transformCamel(d))
      })
      return arr
    } else {
      let obj = data ? {} : data
      for (let key in data) {
        obj[trans(key)] = data[key]
      }
      for (let key in obj) {
        obj[key] = transformCamel(obj[key])
      }
      return obj
    }
  } else {
    return data
  }
}

function trans(str) {
  let regex = /_(\w)(\w)/g
  return str.replace(regex, function ($0, $1, $2) {
    if (typeof $1 === 'number') {
      return $0
    }
    return $1.toUpperCase() + $2
  })
}




